@mixin for-phone-up {
  @media (min-width: 320px) {
    @content;
  }
}
@mixin for-phone-only {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin for-tablet-up {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin for-tablet-only {
  @media (min-width: 768px) and (max-width: 1279px) {
    @content;
  }
}

@mixin for-desktop-up {
  @media (min-width: 1280px) {
    @content;
  }
}

