}

.animation-background {
  background: linear-gradient(to right, #729c95, #92c3ea, #72b4f6) 0 0 / 400% 400%;
  animation: animation-background 10s ease-in-out infinite;
}

@keyframes animation-background {
  0% {
    background-position: 0;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0;
  }
}

.btn-1 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  outline: 2px solid #fff;
  padding: 30px 60px;
  font-family: Sumana, serif;
  text-decoration: none;
  transition: color 1s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn-1:hover {
  color: #526e6e;
}

.btn-1:before {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #fff;
  transition: width 1s;
  position: absolute;
  top: 0;
  left: -50px;
  transform: skewX(35deg);
}

.btn-1:hover:before {
  width: 150%;
}

.btn-2 {
  color: #fff;
  border: 3px solid #fff;
  padding: 20px 50px;
  font-family: Sumana, serif;
  font-size: 25px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.btn-2:before, .btn-2:after {
  content: "";
  width: 40px;
  height: 40px;
  border: inherit;
  transition: all .5s;
  position: absolute;
}

.btn-2:before {
  border-width: 3px 0 0 3px;
  top: -15px;
  left: -15px;
}

.btn-2:after {
  border-width: 0 3px 3px 0;
  bottom: -15px;
  right: -15px;
}

.btn-2:hover:before, .btn-2:hover:after {
  width: calc(100% + 30px);
  height: calc(100% + 30px);
}

.btn-3 {
  text-transform: uppercase;
  outline: 2px solid #fff;
  padding: 30px 60px;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn-3:before {
  content: "";
  width: 5px;
  background: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -10px;
}

.btn-3:hover:before {
  transition: left .5s;
  left: calc(100% + 5px);
}

.btn-4 {
  color: #fff;
  background: #0f2027;
  border: 1px solid #fff;
  border-radius: 8px;
  padding: 20px 50px;
  transition: background 1s, color 1s;
  display: inline-block;
  position: relative;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, .01);
}

.btn-4:after {
  content: "";
  z-index: -1;
  height: 100%;
  width: 100%;
  filter: blur(15px);
  background: linear-gradient(to left, #ff5770, #e4428d, #c42da8, #9e16c3, #6501de, #9e16c3, #c42da8, #e4428d, #ff5770) 0 0 / 200% 200%;
  animation: animateGlow 1.25s linear infinite;
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
  transform: scale(.9)translateZ(0);
}

.btn-4:hover {
  color: #0f2027;
  background: #fff;
}

@keyframes animateGlow {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 200%;
  }
}

.main {
  width: 200px;
  height: 8px;
  z-index: 2;
  border: 1px solid #000;
  border-radius: 10px;
}

.box {
  width: 200px;
  height: 6px;
  background-color: #000;
  border-radius: 10px;
  animation: anima 4s infinite;
}

@keyframes anima {
  from {
    width: 0;
  }

  to {
    width: 200px;
  }
}

.spinner {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: relative;
}

.spinner:before, .spinner:after {
  content: "";
  border-radius: inherit;
  position: absolute;
}

.spinner:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(0deg, #f0c 0%, #339 100%);
  animation: spin .5s linear infinite;
}

.spinner:after {
  width: 85%;
  height: 85%;
  background-color: #526e6e;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.loader-items {
  display: flex;
}

.loader-item + .loader-item {
  margin-left: 5px;
}

.loader {
  width: 40px;
  height: 5px;
  border-radius: 20px;
  display: inline-block;
}

.loader-end {
  background-color: #fff;
}

.loader-not-in-process {
  background-color: rgba(255, 255, 255, .5);
}

.loader-in-process {
  background-color: #fff;
  position: relative;
  overflow: hidden;
}

.loader-in-process:before {
  content: "";
  width: 250%;
  height: 100%;
  border-radius: inherit;
  transform-origin: 0;
  background-image: linear-gradient(332deg, #6b70ff, #f8adff);
  animation: scale 1s infinite;
  position: absolute;
  top: 0;
  left: -50px;
  transform: scaleX(0);
}

@keyframes scale {
  50% {
    transform: scaleX(1);
  }

  100% {
    transform-origin: 100%;
    transform: scaleX(0);
  }
}

.typing-demo {
  width: 22ch;
  white-space: nowrap;
  border-radius: 3px solid;
  animation: typing 2s steps(22, end), blink .5s step-end infinite alternate;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: rgba(0, 0, 0, 0);
  }
}

.fill-text {
  text-transform: uppercase;
  -webkit-text-stroke: .3vw #526e6e;
  display: inline-block;
  position: relative;
}

.fill-text:before {
  content: attr(data-text);
  width: 110%;
  height: 100%;
  color: #fff;
  -webkit-text-stroke: 0vw #526e6e;
  border-right: 2px solid #fff;
  animation: animate 3s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

@keyframes animate {
  0% {
    width: 0;
  }

  70% {
    width: 90%;
  }
}

.icons-wrapper {
