.input-group {
  position: relative;
}

.input {
  font: inherit;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 4px;
  outline: 2px solid #fff;
  padding: 10px;
}

.input-label {
  transition: transform .25s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(10px, 10px);
}

.input:focus + .input-label, .input:valid + .input-label {
  color: #fbdbe1;
  background-color: #526e6e;
  padding-left: 5px;
  padding-right: 5px;
  transform: translate(10px, -14px)scale(.8);
}

.input:-webkit-any(:focus, :valid) {
  outline-color: #fbdbe1;
}

.input:is(:focus, :valid) {
  outline-color: #fbdbe1;
}

.switch {
  height: 34px;
  width: 60px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  width: 26px;
  height: 26px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: #66dd6a;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

